exports.push([module.id, "\n#loginpage .v-alert__icon.mdi.mdi-help-circle-outline {\n    padding-top: 12px;\n}\n", "",{"version":3,"sources":["src/js/components/login.vue","login.vue"],"names":[],"mappings":";AAyHA;IACA,iBAAA;ACvHA","file":"login.vue","sourcesContent":["<template>\n\t<div id=\"loginpage\">\n        <template v-if=\"!show_main_component\">\n            <v-layout row justify-center>\n                <v-alert outlined type=\"info\" style=\"margin-top: 20px;\">\n                    <h3> 歡迎光臨，為了順利進入此系統，請回答以下問題！ </h3>\n                </v-alert>\n            </v-layout>\n\n            <v-layout row justify-center style=\"margin: 10px 0px;\">\n                <v-alert color=\"#2A3B4D\" dark icon=\"mdi-help-circle-outline\" dense style=\"width: 95%;\">\n                    <h2 style=\"white-space: pre-line;\"> {{ question['title'] }} </h2>\n                </v-alert>\n            </v-layout>\n\n            <v-layout row justify-center v-for=\"(answer, a_index) in question['answers']\" :key=\"'answer' + a_index\">\n                <div style=\"margin: 10px 0px; width: 70%\">\n                    <v-btn block :color=\"answer['color']\" dark @click=\"next_question(answer['correct'])\">\n                        <v-icon right dark>mdi-alpha-{{ answer['icon_num'] }}-circle</v-icon>\n                        <strong style=\"padding-left: 20px;\"> {{ answer['content'] }} </strong>\n                        <v-spacer></v-spacer>\n                    </v-btn>\n                </div>\n            </v-layout>\n\n            <v-snackbar v-model=\"right_answer\" top color=\"success\" :timeout=\"1000\">\n                <v-icon color=\"white\" left> mdi-check-circle-outline </v-icon>\n                <strong> 耶～～～你答對了！ </strong>\n                <v-spacer></v-spacer>\n            </v-snackbar>\n\n            <v-snackbar v-model=\"wrong_answer\" top color=\"error\" :timeout=\"1000\">\n                <v-icon color=\"white\" left> mdi-alert-circle-outline </v-icon>\n                <strong> 喔不...你答錯了哦！ </strong>\n                <v-spacer></v-spacer>\n            </v-snackbar>\n        </template>\n\n        <Main v-if=\"show_main_component\"/>\n\t</div>\n</template>\n\n<script>\nimport Main from './main.vue';\n\nexport default {\n\tdata: function() {\n\t\treturn {\n            show_main_component: false,\n            right_answer: false,\n            wrong_answer: false,\n\n            question_index: 0,\n            question_max_index: 2,\n            questions: [{\n                \"title\": \"請問妳是誰？\", \n                \"answers\": [\n                    {\"icon_num\": \"a\", \"content\": \"火鍋愛好者\", \"color\": \"#9c27b0\", \"correct\": false},\n                    {\"icon_num\": \"b\", \"content\": \"Shiuan\", \"color\": \"#fb8c00\", \"correct\": false},\n                    {\"icon_num\": \"c\", \"content\": \"大睡豬\", \"color\": \"#4baf50\", \"correct\": false},\n                    {\"icon_num\": \"d\", \"content\": \"活潑の隊輔\", \"color\": \"#ff5251\", \"correct\": false},\n                    {\"icon_num\": \"e\", \"content\": \"以上皆是\", \"color\": \"#2196f3\", \"correct\": true}\n                ]\n            }, {\n                \"title\": \"請問妳的生日是？\", \n                \"answers\": [\n                    {\"icon_num\": \"a\", \"content\": \"12/22\", \"color\": \"#4baf50\", \"correct\": false},\n                    {\"icon_num\": \"b\", \"content\": \"12/23\", \"color\": \"#2196f3\", \"correct\": false},\n                    {\"icon_num\": \"c\", \"content\": \"12/24\", \"color\": \"#9c27b0\", \"correct\": true},\n                    {\"icon_num\": \"d\", \"content\": \"12/25\", \"color\": \"#ff5251\", \"correct\": false},\n                    {\"icon_num\": \"e\", \"content\": \"12/26\", \"color\": \"#fb8c00\", \"correct\": false}\n                ]\n            }, {\n                \"title\": \"請問今天是什麼日子？\", \n                \"answers\": [\n                    {\"icon_num\": \"a\", \"content\": \"阿軒的生日\", \"color\": \"#fb8c00\", \"correct\": false},\n                    {\"icon_num\": \"b\", \"content\": \"スアンの誕生日\", \"color\": \"#9c27b0\", \"correct\": false},\n                    {\"icon_num\": \"c\", \"content\": \"쉬안 탄생일\", \"color\": \"#2196f3\", \"correct\": false},\n                    {\"icon_num\": \"d\", \"content\": \"Shiuan's birthday\", \"color\": \"#ff5251\", \"correct\": false},\n                    {\"icon_num\": \"e\", \"content\": \"以上皆是\", \"color\": \"#4baf50\", \"correct\": true}\n                ]\n            }]\n\t\t}\n\t},\n\n\tcreated: function() {\n\n\t},\n\t\n\tcomponents: {\n        Main\n\t},\n\t\n\tcomputed: {\n\t\tquestion: function() {\n            return this.questions[this.question_index];\n        }\n\t},\n\t\n\twatch: {\n\n\t},\n\n\tmethods: {\n\t\tnext_question: function(answer) {\n            if(this.question_index == this.question_max_index && answer) {\n                this.show_main_component = true;\n            }\n            else if(answer) {\n                this.question_index += 1;\n                this.right_answer = true;\n            }\n            else {\n                this.wrong_answer = true;\n            }\n        }\n\t}\n  }\n</script>\n\n<style>\n    #loginpage .v-alert__icon.mdi.mdi-help-circle-outline {\n        padding-top: 12px;\n    }\n</style>","\n#loginpage .v-alert__icon.mdi.mdi-help-circle-outline {\n    padding-top: 12px;\n}\n"]}]);

